body{
  max-width: 500px;
  margin-top: 160px;
  margin-left: auto;
  margin-right: auto;
}

.app{
  align-items: center;
}


input[type=text] {
  width: 100%;
  padding: 12px 20px;
  margin: 8px 0;
  box-sizing: border-box;
  border: 3px solid #ccc;
  -webkit-transition: 0.5s;
  transition: 0.5s;
  outline: none;
}

input[type=text]:focus {
  border: 3px solid #555;
}

.form-group{
    align-items: center;
}

.list-group{
  width: 100%;
  padding: 12px 20px;
  margin: 8px 0;
  box-sizing: border-box;
  /* border: 3px solid #ccc; */
  box-shadow: 1px 1px 3px #ccc;
  outline: none;
}

.list-group-item{
    list-style: none; 
}

.list-group-item:hover {
  list-style: none;
  background-color: #ccc;
}